@import "~@splidejs/splide/dist/css/splide.min.css";
@import "variables";

.splide {

    .splide__arrow--prev {
        @media (min-width: $md) {
            left: 4em;
        }

    }

    .splide__arrow--next {
        @media (min-width: $md) {
            right: 4em;
        }
    }

    .splide__arrow {
        width: 3em;
        height: 3em;
        border-radius: 0;
        background: none;
        opacity: 1;

        &:disabled {
            opacity: .3;
        }

        svg {
            width: 1.5em;
            height: 1.5em;
        }

        &:hover {
            svg {
                fill: $secondary-color;
            }
        }
    }

    .splide__pagination {
        .splide__pagination__page {
            background: $primary-color;
            border: 1px solid $primary-color;
            width: 10px;
            height: 10px;
            border-radius: 0;
            opacity: 1;

            &.is-active {
                background: $base-color;
                transform: scale(1);
                opacity: 1;
            }
        }
    }
}